import React from 'react';

interface RatingProps {
  rating: number;
  count: number;
}

export const Rating: React.FC<RatingProps> = ({ rating, count }) => {
  // Calculate full, half, and empty stars
  const fullStars = Math.floor(rating);
  const halfStar = rating % 1 >= 0.5;
  const emptyStars = 5 - fullStars - (halfStar ? 1 : 0);

  return (
    <div className="flex items-center space-x-1" aria-label={`Rating: ${rating} out of 5`}>
      <div className="flex">
        {Array(fullStars)
          .fill(null)
          .map((_, idx) => (
            <span key={`full-${idx}`} className="text-yellow-400" aria-hidden="true">
              ★
            </span>
          ))}
        {halfStar && (
          <span className="text-yellow-400" aria-hidden="true">
            ☆
          </span>
        )}
        {Array(emptyStars)
          .fill(null)
          .map((_, idx) => (
            <span key={`empty-${idx}`} className="text-yellow-400" aria-hidden="true">
              ☆
            </span>
          ))}
      </div>
      <span className="text-sm text-gray-600">({count} ratings)</span>
    </div>
  );
};

export default Rating;
